<script>
/* eslint-disable no-unused-vars */
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import { Button, Slider, Select } from 'ant-design-vue';

import pako from 'pako';

const textEncoder = new TextEncoder('utf-8');
const textDecoder = new TextDecoder('utf-8');

const readInt = function (buffer, start, len) {
  let result = 0
  for (let i = len - 1; i >= 0; i--) {
    result += Math.pow(256, len - i - 1) * buffer[start + i]
  }
  return result
}

const writeInt = function (buffer, start, len, value) {
  let i = 0
  while (i < len) {
    buffer[start + i] = value / Math.pow(256, len - i - 1)
    i++
  }
}

const encode = function (str, op) {
  let data = textEncoder.encode(str);
  let packetLen = 16 + data.byteLength;
  let header = [0, 0, 0, 0, 0, 16, 0, 1, 0, 0, 0, op, 0, 0, 0, 1]
  writeInt(header, 0, 4, packetLen)
  return (new Uint8Array(header.concat(...data))).buffer
}
const decode = function (blob) {
  return new Promise(function (resolve) {
    let reader = new FileReader();
    reader.onload = function (e) {
      let buffer = new Uint8Array(e.target.result)
      let result = {}
      result.packetLen = readInt(buffer, 0, 4)
      result.headerLen = readInt(buffer, 4, 2)
      result.ver = readInt(buffer, 6, 2)
      result.op = readInt(buffer, 8, 4)
      result.seq = readInt(buffer, 12, 4)
      if (result.op === 5) {
        result.body = []
        let offset = 0;
        while (offset < buffer.length) {
          let packetLen = readInt(buffer, offset, 4)
          let headerLen = 16 // readInt(buffer,offset + 4,4)
          let data = buffer.slice(offset + headerLen, offset + packetLen);
          let body = '';
          try {
            // pakoÂèØËÉΩÊó†Ê≥ïËß£Âéã
            body = textDecoder.decode(pako.inflate(data));
          } catch (e) {
            body = textDecoder.decode(data)
          }

          if (body) {
            // Âêå‰∏ÄÊù° message ‰∏≠ÂèØËÉΩÂ≠òÂú®Â§öÊù°‰ø°ÊÅØÔºåÁî®Ê≠£ÂàôÁ≠õÂá∫Êù•
            // eslint-disable-next-line no-control-regex
            const group = body.split(/[\x00-\x1f]+/);
            group.forEach(item => {
              try {
                const parsedItem = JSON.parse(item);
                if (typeof parsedItem === 'object') {
                  result.body.push(parsedItem);
                } else {
                  // ËøôÈáåitemÂèØËÉΩ‰ºöËß£ÊûêÂá∫number
                  // Ê≠§Êó∂ÂèØ‰ª•Â∞ùËØïÈáçÊñ∞Áî®pakoËß£ÂéãdataÔºàÊê∫Â∏¶ËΩ¨Êç¢ÂèÇÊï∞Ôºâ
                  // const newBody = textDecoder.decode(pako.inflate(data, {to: 'String'}))
                  // ÈáçÂ§ç‰∏äÈù¢ÁöÑÈÄªËæëÔºåÁ≠õÈÄâÂèØËÉΩÂ≠òÂú®ÁöÑÂ§öÊù°‰ø°ÊÅØ
                  // ÂàùÊ≠•È™åËØÅÔºåËøôÈáåÂèØ‰ª•Ëß£ÊûêÂà∞INTERACT_WORD„ÄÅDANMU_MSG„ÄÅONLINE_RANK_COUNT
                  // SEND_GIFT„ÄÅSUPER_CHAT_MESSAGE
                }
              } catch (e) {
                // ÂøΩÁï•Èùû JSON Â≠óÁ¨¶‰∏≤ÔºåÈÄöÂ∏∏ÊÉÖÂÜµ‰∏ã‰∏∫ÂàÜÈöîÁ¨¶
              }
            });
          }

          offset += packetLen;
        }
      } else if (result.op === 3) {
        result.body = {
          count: readInt(buffer, 16, 4)
        };
      }
      resolve(result)
    }
    reader.readAsArrayBuffer(blob);
  });
}

var synth = window.speechSynthesis;
var matches;
var utterance;
var ws;
var intervalId;

// Â§©ÁéÑ‰πã‰∫∫ÂºπÂπïÔºåËøáÊª§Áî®
let anchorLotDanmu = {}

// userName, giftName, num, timeoutId
let giftMessageQueue = [];

let filters = [
  /^www$/,
  // ÂÖ®Êñá‰∏çÂåÖÂê´‰∏≠ÊñáËã±ÊñáÊï∞Â≠óÔºåÂèØËÉΩÊòØÈ¢úÊñáÂ≠ó
  /^[^\w\u4e00-\u9fa5]+$/
];

function filterDanmu(message) {
  return message.length < 2 || filters.some(exp => message.match(exp)) || Object.values(anchorLotDanmu).includes(message);
}

function optimizeDanmu(message) {
  return message.replace(/(?<!\d)233+(?!\d)/g, function (s) {
    return "‰∫å".padEnd(s.length, "‰∏â")
  })
}

export default {
  name: "Home",
  data() {
    return {
      voices: [],
      value: "",
      value1: 20,
      disabled: false,
      defaultSelect: "ÈÄâÊã©ÊúóËØªËØ≠Ë®ÄÁ±ªÂûã",
      speedSelect: "x1",
      inputText: "",
      readTextLog: "",
      roomId: "",
      selectIdx: 0,
      selectVoice: {},

      // ËØ≠Èü≥ÂÄçÈÄü
      rateValue: 1.25,
      minRate: 0.5,
      maxRate: 2,
      rateMarks: {
        0.5: "ÁºìÊÖ¢",
        0.75: "",
        1: "Â∏∏ËßÑ",
        1.25: "",
        1.5: "",
        1.75: "",
        2: "Âø´",
      },

      // Èü≥Èáè
      volumeValue: 100,

      // ÊúóËØªÊ¶ÇÁéá
      readRate: 100,

      // Èü≥Ë∞ÉÈ´ò‰Ωé
      pitchValue: 1,
      minPitch: 0,
      maxPitch: 2,
      pitchMarks: {
        0: "",
        2: "",
      },
      recordStatus: false,
      readDanmu: true,
      readGift: false,
      readWelcome: false,
    };
  },
  created() {
  },
  beforeMount() {
  },
  mounted() {
    this.populateVoiceList();
    this.checkBrowser();
    if (localStorage.roomId) {
      this.roomId = localStorage.roomId;
    }
    if (localStorage.readDanmu) {
      this.readDanmu = localStorage.readDanmu === 'true';
    }
    if (localStorage.readGift) {
      this.readGift = localStorage.readGift === 'true';
    }
    if (localStorage.readWelcome) {
      this.readWelcome = localStorage.readWelcome === 'true';
    }
    if (localStorage.readRate) {
      this.readRate = parseInt(localStorage.readRate);
    }
    if (localStorage.rateValue) {
      this.rateValue = parseFloat(localStorage.rateValue);
    }
    if (localStorage.volumeValue) {
      this.volumeValue = parseInt(localStorage.volumeValue);
    }
  },
  watch: {
    roomId(newValue) {
      localStorage.roomId = newValue;
    },
    readDanmu(newValue) {
      localStorage.readDanmu = newValue;
    },
    readGift(newValue) {
      localStorage.readGift = newValue;
    },
    readWelcome(newValue) {
      localStorage.readWelcome = newValue;
    },
    readRate(newValue) {
      localStorage.readRate = newValue;
    },
    rateValue(newValue) {
      localStorage.rateValue = newValue;
    },
    volumeValue(newValue) {
      localStorage.volumeValue = newValue;
    }
  },
  updated() {
  },
  beforeDestroy() {
  },
  methods: {
    // Ëé∑ÂèñËØ≠Ë®ÄÊîØÊåÅ
    populateVoiceList: function () {
      if (typeof speechSynthesis === "undefined") {
        return;
      }
      new Promise((resolve, reject) => {
        setTimeout(() => {
          let voices = speechSynthesis.getVoices();

          resolve(voices);
        }, 0);
      }).then((voices) => {
        this.voices = voices
            .map((c) => {
              if (c.name.startsWith("Google ")) {
                c.displayName = c.name.replace(/^Google /, "");
              } else if (c.name.startsWith("Microsoft")) {

                matches = c.name.match(/^Microsoft (.+) Online.*- (.+)/);
                // c.displayName = `${matches[2]} - ${matches[1]}`;
                c.displayName = c.name
              } else {
                c.displayName = c.name;
              }
              return c;
            })
            .sort(function (a, b) {
              let aName = a.displayName.replace(/^.+-/, "");
              let bName = b.displayName.replace(/^.+-/, "");
              return aName.localeCompare(bName);
            });
        let xiaoxiaoIndex = this.voices.findIndex((c) => c.displayName.includes("Xiaoxiao"));
        if (xiaoxiaoIndex >= 0) {
          this.selectIdx = xiaoxiaoIndex;
        }
      });
    },
    test() {
      synth.resume()
      this.$message.success('ÁªßÁª≠Êí≠Êîæ');
    },
    onClick() {
      if (!this.inputText || this.inputText.length === 0) {
        return;
      }

      this.speak(this.inputText);
    },
    onConnect() {
      if (ws) {
        ws.close();
        clearInterval(intervalId);
      }
      let roomId = this.roomId.replace("https://live.bilibili.com/", "");
      let biliApi = encodeURIComponent(`https://api.live.bilibili.com/room/v1/Room/room_init?id=${roomId}`);
      fetch(`https://json2jsonp.com/?url=${biliApi}&callback=result`).then(function (response) {
        return response.text();
      }).then(jsonp => {
        let json = jsonp.replace(/^result\((.+)\)$/, "$1");
        let body = JSON.parse(json);
        console.log(`roomId ‰ªé ${roomId} ËΩ¨‰∏∫ ${body.data.room_id}`);
        let realRoomId = body.data.room_id;
        ws = new WebSocket('wss://broadcastlv.chat.bilibili.com/sub');
        ws.onopen = () => {
          ws.send(encode(JSON.stringify({
            roomid: parseInt(realRoomId)
          }), 7));
          console.log("connect roomid: " + roomId);
          this.speak("ËøûÊé•ÊàøÈó¥Ôºö" + roomId);
          if (!body.data.live_status) {
            this.speak("‰∏ªÊí≠Ê≠£Âú®Êë∏È±º");
          }
        };
        intervalId = setInterval(function () {
          ws.send(encode('', 2));
        }, 30000);
        ws.onmessage = async msgEvent => {
          const packet = await decode(msgEvent.data);
          switch (packet.op) {
            case 8:
              console.log('ws.onmessageÔºöÂä†ÂÖ•ÊàøÈó¥');
              break;
            case 3:
              // eslint-disable-next-line no-case-declarations
              const count = packet.body.count;
              console.log(`‰∫∫Ê∞îÔºö${count}`);
              break;
            case 5:
              packet.body.forEach((msg) => {
                console.log(msg);
                switch (msg.cmd) {
                  case 'ANCHOR_LOT_START':
                    // Â§©ÈÄâ‰πã‰∫∫ÂºÄÂßã
                    anchorLotDanmu[msg.data.id] = msg.data.danmu;
                    console.log("Â§©ÈÄâ‰πã‰∫∫ÂºÄÂßãÔºåÂºπÂπïÔºö" + msg.data.danmu);
                    break;
                  case 'ANCHOR_LOT_END':
                    // Â§©ÈÄâ‰πã‰∫∫ÁªìÊùü
                    console.log("Â§©ÈÄâ‰πã‰∫∫ÁªìÊùüÔºåÂºπÂπïÔºö" + anchorLotDanmu[msg.data.id]);
                    anchorLotDanmu[msg.data.id] = undefined;
                    break;
                  case 'DANMU_MSG':
                    // ÊôÆÈÄöÂºπÂπï
                    if (this.readDanmu) {
                      let userName = msg.info[2][1].replace(/_*bili_*/g, "");

                      // Âõ∫ÂÆöÊàëÂíå‰∏≤‰∏≤ÁöÑÁî®Êà∑ÂêçÔºàÂ•πÁöÑÂêçÂ≠óÂ§™Èïø‰∫ÜÔºâ
                      // TODO: Â¢ûÂä†‰∏Ä‰∏™ÊñáÊú¨Ê°ÜÈÖçÁΩÆËá™ÂÆö‰πâÁî®Êà∑Âêç
                      let userId = msg.info[2][0];
                      if (userId === 12695494) {
                        userName = "‰∏≤‰∏≤";
                      } else if (userId === 21222) {
                        userName = "DJ";
                      }

                      console.log(msg.info[2]);
                      let message = msg.info[1];
                      let text = `${userName}ËØ¥: ${message}`;
                      if (this.readRate >= Math.round(Math.random() * 100)) {
                        // TODO: Â¢ûÂä†‰∏Ä‰∏™ÊñáÊú¨Ê°ÜÈÖçÁΩÆËøáÊª§ËØçÊ±á
                        if (filterDanmu(message)) {
                          console.log(`Êó†ÊÑè‰πâË¢´ËøáÊª§ÁöÑÂºπÂπïÔºö${text}`);
                        } else {
                          text = optimizeDanmu(text);
                          this.speak(text);
                        }
                      } else {
                        console.log(`Âõ†‰∏∫ÊúóËØªÊ¶ÇÁéáÂøΩÁï•ÁöÑÂºπÂπïÔºö${text}`);
                      }
                    }
                    break;
                  case 'SUPER_CHAT_MESSAGE':
                  case 'SUPER_CHAT_MESSAGE_JPN':
                    // ÈÜíÁõÆÁïôË®ÄÔºàÊàë‰πü‰∏çÁü•ÈÅìÊòØÂï•‰∏úË•øÔºâ
                    if (this.readDanmu) {
                      let text = `Êî∂Âà∞Êù•Ëá™${msg.data.user_info.uname}ÁöÑÈÜíÁõÆÁïôË®ÄÔºö${msg.data.message}`;
                      this.speak(text);
                    }
                    break;
                  case 'USER_TOAST_MSG':
                    // Áª≠Ë¥πËà∞ÈïøÔºü
                    if (this.readDanmu) {
                      let text = msg.data.toast_msg.replace("<%", "").replace("%>", "");
                      this.speak(text);
                    }
                    break;
                  case 'SEND_GIFT':
                    // Á§ºÁâ©
                    if (this.readGift) {
                      let userName = msg.data.uname;
                      let giftName = msg.data.giftName;
                      let num = msg.data.num;
                      let text = `Êî∂Âà∞Êù•Ëá™${userName}ÁöÑ${num}‰∏™${giftName}`;
                      let giftMessage = giftMessageQueue.find(e => e.userName === userName && e.giftName === giftName);
                      if (giftMessage) {
                        clearTimeout(giftMessage.timeoutId);
                        giftMessage.num += num;
                        text = `Êî∂Âà∞Êù•Ëá™${userName}ÁöÑ${giftMessage.num}‰∏™${giftName}`;
                        console.log(`ÂêàÂπ∂Á§ºÁâ©‰ø°ÊÅØÔºö${text}`);
                      } else {
                        giftMessage = {
                          userName: userName,
                          giftName: giftName,
                          num: num
                        };
                        giftMessageQueue.push(giftMessage);
                      }
                      giftMessage.timeoutId = setTimeout(() => {
                        this.speak(text);
                        giftMessageQueue = giftMessageQueue.filter(e => e !== giftMessage);
                      }, 5000);
                    }
                    break;
                    // ‰∏äËà∞
                  case 'GUARD_BUY':
                    // eslint-disable-next-line no-case-declarations
                    let text = `Ê¨¢Ëøé${msg.data.username}Âä†ÂÖ•Â§ßËà™Êµ∑`;
                    this.speak(text);
                    break;
                    // Ê¨¢ËøéËÄÅÁà∑ÂíåËà∞Èïø
                  case 'WELCOME':
                    if (this.readWelcome) {
                      let text = `Ê¨¢Ëøé${msg.data.uname}`;
                      this.speak(text);
                    }
                    break;
                    // Ê≠§Â§ÑÁúÅÁï•ÂæàÂ§öÂÖ∂‰ªñÈÄöÁü•Á±ªÂûã
                }
              })
              break;
            default:
              console.log(packet);
          }
        };
      });
    },
    onDisconnect(e) {
      if (ws) {
        ws.close();
        clearInterval(intervalId);
        giftMessageQueue.forEach(msg => clearTimeout(msg.timeoutId));
        giftMessageQueue.length = 0;
        synth.cancel();
        this.speak("Êñ≠ÂºÄÊàøÈó¥Ôºö" + this.roomId.replace("https://live.bilibili.com/", ""));
      }
    },
    onReadRateSlider(e) {
      this.readRate = e;
    },
    onInput(e) {
      this.inputText = e.target.value;
    },
    onInputReadTextLog(e) {
      this.readTextLog = e.target.value;
    },
    onInputRoomId(e) {
      this.roomId = e.target.value;
    },
    // select ÈÄâ‰∏≠
    onSelect(index) {
      this.selectIdx = index;
    },
    speak(text) {
      let {
        voices,
        selectIdx,
        rateValue,
        volumeValue,
        pitchValue
      } = this;

      // if (filterDanmu(text)) {
      //   console.log("ËøáÊª§ÂºπÂπï" + text);
      //   return;
      // }
      //
      // text = optimizeDanmu(text);

      this.readTextLog = `${new Date().toLocaleTimeString()}\t${text}\n${this.readTextLog}`;

      if (volumeValue === 0) {
        return;
      }

      utterance = new SpeechSynthesisUtterance(text);
      utterance.onstart = () => {
        console.log(`SpeechSynthesisUtterance.onstart: ${text}`);
      };
      utterance.onerror = () => {
        console.error(`SpeechSynthesisUtterance.onerror: ${text}`);
      };
      utterance.voice = voices[selectIdx]; // ËÆæÁΩÆËØ≠Èü≥ÂºïÊìé
      utterance.pitch = pitchValue;
      utterance.rate = rateValue;
      utterance.volume = volumeValue / 100.0;
      synth.speak(utterance);
    },
    onChange(value) {
      console.log("change: ", value);
      if (!this.recordStatus) {
        // ËØ∑Ê±ÇÂΩïÈü≥ÊéàÊùÉ
        this.initRecorder()
      }
      this.recordStatus = !this.recordStatus
    },
    onChangeDanmu(value) {
      console.log("onChangeDanmu: ", value);
      this.readDanmu = !this.readDanmu
    },
    onChangeGift(value) {
      console.log("onChangeGift: ", value);
      this.readGift = !this.readGift
    },
    onChangeWelcome(value) {
      console.log("onChangeWelcome: ", value);
      this.readWelcome = !this.readWelcome
    },
    onChangeVoice(value) {
      console.log("onChangeVoice: ", value);
      this.selectVoice = value.displayName;
    },
    onAfterChange(value) {
      console.log("afterChange: ", value);
    },
    // ÊîπÂèòËØ≠Èü≥ÈÄüÂ∫¶
    onRateSlider(e) {
      this.rateValue = e;
    },
    onVolumeSlider(e) {
      this.volumeValue = e;
    },
    // ÊîπÂèòÈü≥Ë∞ÉÈ´ò‰Ωé
    onPitchSlider(e) {
      this.pitchValue = e;
    },
    checkBrowser() {
      // console.log('userAgent: '+window.navigator.userAgent)
    },

    // Êñ∞Âª∫ MediaRecorderÂØπË±°
    initRecorder() {
      const _this = this
      var constraints = window.constraints = {
        audio: true,
        video: false
      };
      navigator.mediaDevices.getUserMedia(constraints)
          .then(function (stream) {
            /* use the stream */
            _this.recorderFn(stream)
            console.log(stream)

          })
          .catch(function (err) {
            console.log(err)
            _this.recordStatus = false
            _this.$message.warning('üôÖüèª‚Äç‚ôÄÔ∏è ÊãíÁªùÂΩïÈü≥ÊùÉÈôê')
            /* handle the error */
          });
    },
    // ÂàõÂª∫ÂΩïÈü≥ÂÆû‰æã
    recorderFn(stream) {
      let mediaRecorder = new MediaRecorder(stream);
      mediaRecorder.start();
      console.log('ÂΩïÈü≥‰∏≠...')

    },
  }
  ,
}
;
</script>
